/*
 * 这里是给不知道怎么注册使用的小伙伴友情提示...
 * main.js中全局注册
 * 注册方法 ： import Cname from '文件路径'
 *            Vue.Component('c-name',Cname)
 * 在页面中直接使用 ： <c-name></c-name>
 * 
 * 页面中注册： 
 * 	import Cname from '文件路径'
 * 	components:{minDescInput}
 * 使用： <c-name></c-name>
 * 		
 */
import minDescInput from './min-desc-input.vue'
export default {
	components: {
		minDescInput
	},
	props: {
		list: {
			type: [Array, String],
			default: () => []
		},
		desc: {
			type: String,
			default: '企业人力资源管理'
		},
		sign: {
			type: String,
			default: ''
		},
		placeholder: {
			type: String,
			default: '查看分级'
		},
		disabled: {
			type: Boolean,
			default: true
		},
		border: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			isShow: true,
			current: Number,
			value: ''
			// data: ['8:00', '23:00', '9:00']
		}
	},
	computed: {
		// isArray() {
		// 	if (toString.call(this.list) === '[object Array]') {
		// 		return true
		// 	}
		// 	return false
		// }
	},
	watch: {
		value(a) {
			this.$emit('input', a)
		}
	},
	methods: {
		test() {
			this.isShow = !this.isShow
		},
		chioce(n) {
			if (toString.call(this.list) === '[object Array]') {
				this.current = n
				this.value = this.list[n]
			}
		}
	}
}
